<template>
  <div>
  <el-carousel :interval="4000"
               arrow="always"
               height="500px"
               trigger="click"
               ref="carousel"
               @mouseenter.native="delHandleMouseEnter">
    <el-carousel-item v-for="item in banner" :key="item.id">
      <el-image
          style="width: 100%;height:100%"
          :src="item.src"
          ref="imgHeight"
          oncontextmenu="return false"
          ondragstart="return false"
          fit="cover">
      </el-image>
    </el-carousel-item>
  </el-carousel>
  </div>
</template>

<script>
export default {

  data() {
    return {
      banner:[
        {src:"https://www.itheima.com/images/slidead/HOMEPAGE/20230104110331%E9%A6%96%E9%A1%B5%E5%B9%BB%E7%81%AF1920-432.png"},
        {src:"https://iflyssedata.oss-cn-shanghai.aliyuncs.com/images/20220917034916.png"},
        {src:"https://iflyssedata.oss-cn-shanghai.aliyuncs.com/images/20220512061508.png"},
      ],
    };
  },
  mounted(){
    this.$refs.carousel.handleMouseEnter = () => { };
  },
  methods: {
    delHandleMouseEnter() {
      this.$refs.carousel.handleMouseEnter = () => { };
    },
    getBannerUrl(){
      this.$http({
        url:"http://rap2api.taobao.org/app/mock/309060/banner",
        method:'get',
      }).then(res=>{
        if (res.data.code=200){
          this.banner=res.data.data.banner
        }
      })
    },
  },
  created() {
    // this.getBannerUrl()
  }
}
</script>

<style scoped>
>>>.el-carousel__button {
  width: 15px;
  height: 15px;
  border-radius: 10px;
}

>>>.el-carousel__indicator.is-active button {
  background: #F5A625;
  width: 30px;
}

</style>

